export default "header {\n  font-family: \"Roboto\", sans-serif;\n  color: #828282;\n  font-size: 1.2rem;\n  padding: 30px;\n}\n\n.headerLine {\n  background-color: #fbfbfb;\n  width: 100%;\n}\n\n.serviceSettingsBar {\n  padding: 15px;\n}\n\n.pcSelectButton {\n  display: block;\n}\n\n.checkboxContainer {\n  display: block;\n  position: relative;\n  padding-left: 35px;\n  margin-bottom: 12px;\n  cursor: pointer;\n  font-family: \"Roboto\", sans-serif;\n  color: #acaaaa;\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  user-select: none;\n}\n\n/* Hide the browser's default checkbox */\n\n.checkboxContainer input {\n  position: absolute;\n  opacity: 0;\n  cursor: pointer;\n  height: 0;\n  width: 0;\n}\n\n/* Create a custom checkbox */\n\n.checkmark {\n  position: absolute;\n  top: 0;\n  left: 0;\n  height: 22px;\n  width: 22px;\n  border: solid 1px #c9c7c7;\n  border-radius: 8px;\n  background-color: rgba(245, 245, 245, 0.6);\n}\n\n/* On mouse-over, add a grey background color */\n\n.checkboxContainer:hover input ~ .checkmark {\n  background-color: whitesmoke;\n}\n\n/* Create the checkmark/indicator (hidden when not checked) */\n\n.checkmark:after {\n  content: \"\";\n  position: absolute;\n  display: none;\n}\n\n/* Show the checkmark when checked */\n\n.checkboxContainer input:checked ~ .checkmark:after {\n  display: block;\n}\n\n/* Style the checkmark/indicator */\n\n.checkboxContainer .checkmark:after {\n  left: 8px;\n  top: 5px;\n  width: 5px;\n  height: 10px;\n  border: solid black;\n  border-width: 0 3px 3px 0;\n  transform: rotate(45deg);\n}\n\n.multipleChoicesButton {\n  background-image: linear-gradient(to left, #0491ed, #0e65d8);\n  box-shadow: 0 3px 22px 0 rgba(0, 0, 0, 0.22);\n  font-family: \"Lato\", sans-serif;\n  padding: 8px 23px 8px 26px;\n  border-radius: 32px;\n  line-height: 1.19;\n  font-weight: 900;\n  font-size: 14px;\n  color: white;\n}\n\n.serviceMainContainer {\n  margin-top: -60px;\n  margin-left: 80px;\n}\n\n.serviceColumn {\n  margin-top: 80px;\n}\n\n.serviceCard {\n  width: 305px;\n  height: 160px;\n  border-radius: 32px;\n  box-shadow: 0 3px 22px 0 rgba(0, 0, 0, 0.22);\n  background-color: #fcfcfc;\n  padding-top: 60px;\n  cursor: pointer;\n  transition: all 0.5s;\n  -webkit-transition: all 0.5s;\n  -moz-transition: all 0.5s;\n  -o-transition: all 0.5s;\n}\n\n.serviceCard h1 {\n  font-family: Lato;\n  font-size: 24px;\n  font-weight: 900;\n  font-stretch: normal;\n  font-style: normal;\n  color: #4590f3;\n  text-align: center;\n}\n\n.serviceCard:hover, .serviceCard:active {\n  transform: scale(1.1);\n  transition: all 0.5s;\n  -webkit-transition: all 0.5s;\n  -moz-transition: all 0.5s;\n  -o-transition: all 0.5s;\n}\n\n.textAlignRight {\n  text-align: right;\n}\n\n.textAlignLeft {\n  text-align: left;\n}\n\n@media only screen and (max-device-width: 500px) {\n  header {\n    font-size: 1.1rem;\n    padding: 3px;\n  }\n\n  .pcSelectButton {\n    display: none;\n  }\n\n  .multipleChoicesButton {\n    font-size: 12px;\n  }\n\n  .serviceMainContainer {\n    margin-left: 45px;\n  }\n\n  .serviceCard {\n    width: 250px;\n    height: 155px;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"